@font-face {
    font-family: 'm6x11plus';
    src: url('./fonts/m6x11plus.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'm6x11plus', monospace;
    letter-spacing: 0.5px;
    cursor: default;
    -webkit-user-select: none;
    user-select: none;
}

.Heart {
    color: #f11b52;
}

.Diamond {
    color: #f15a27;
}

.Club {
    color: #074540;
}

.Spades {
    color: #292189;
}

.chip {
    color: #0093ff;
}

.mult {
    color: #ff4c40;
}

/* làm border-radius dạng pixel Nguồn https://pixelcorners.lukeb.co.uk/?radius=8&multiplier=4 */
.pixel-corners {
    clip-path: polygon(0px calc(100% - 10px),
            2px calc(100% - 10px),
            2px calc(100% - 6px),
            4px calc(100% - 6px),
            4px calc(100% - 4px),
            6px calc(100% - 4px),
            6px calc(100% - 2px),
            10px calc(100% - 2px),
            10px 100%,
            calc(100% - 10px) 100%,
            calc(100% - 10px) calc(100% - 2px),
            calc(100% - 6px) calc(100% - 2px),
            calc(100% - 6px) calc(100% - 4px),
            calc(100% - 4px) calc(100% - 4px),
            calc(100% - 4px) calc(100% - 6px),
            calc(100% - 2px) calc(100% - 6px),
            calc(100% - 2px) calc(100% - 10px),
            100% calc(100% - 10px),
            100% 10px,
            calc(100% - 2px) 10px,
            calc(100% - 2px) 6px,
            calc(100% - 4px) 6px,
            calc(100% - 4px) 4px,
            calc(100% - 6px) 4px,
            calc(100% - 6px) 2px,
            calc(100% - 10px) 2px,
            calc(100% - 10px) 0px,
            10px 0px,
            10px 2px,
            6px 2px,
            6px 4px,
            4px 4px,
            4px 6px,
            2px 6px,
            2px 10px,
            0px 10px);
}


/* animation text */
.wave-text span {
    display: inline-block;
    /* font-size: 4rem; */
    animation: wave 3s ease-in-out infinite;
}

.wave-text span:nth-child(1) {
    animation-delay: 0s;
}

.wave-text span:nth-child(2) {
    animation-delay: 0.2s;
}

.wave-text span:nth-child(3) {
    animation-delay: 0.4s;
}

.wave-text span:nth-child(4) {
    animation-delay: 0.6s;
}

.wave-text span:nth-child(5) {
    animation-delay: 0.8s;
}

.wave-text span:nth-child(6) {
    animation-delay: 1s;
}

.wave-text span:nth-child(7) {
    animation-delay: 1.2s;
}

.wave-text span:nth-child(8) {
    animation-delay: 1.4s;
}

.wave-text span:nth-child(9) {
    animation-delay: 1.6s;
}

.wave-text span:nth-child(10) {
    animation-delay: 1.8s;
}

.wave-text span:nth-child(11) {
    animation-delay: 2s;
}

@keyframes wave {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-5px);
    }
}

.gold {
    color: #f2b544;
    font-size: 30px;
}

/* Them animation khi tang diem chip hoac mult */
@keyframes bounce {
    0%   { transform: scale(1.6) rotateZ(-15deg); }
    5.55%  { transform: scale(1.8) rotateZ(-20deg); }
    11.1%  { transform:scale(1.5) rotateZ(-17deg); }
    16.65%  { transform:scale(1);}
    22.2%  { transform: rotateZ(7deg); }
    27.75%  { transform:scale(1.1) rotateZ(8deg); }
    33.3%  { transform:scale(1.2) rotateZ(8deg); }
    38.85%  { transform:scale(1.2) rotateZ(0deg); }
    44.4%  { transform:scale(1.2) rotateZ(-3deg); }
    49.95%  { transform:scale(1.1) rotateZ(-5deg); }
    55.5%  { transform:scale(1.0) rotateZ(-5deg); }
    61.05%  { transform:scale(1.0) rotateZ(-3deg); }
    66.6%  { transform:scale(1) rotateZ(0deg); }
    72.15%  { transform:scale(1.05) rotateZ(2deg); }
    77.7%  { transform:scale(1.05) rotateZ(3deg); }
    83.25%  { transform:scale(1.05) rotateZ(2deg); }
    88.8%  { transform:scale(1.0) rotateZ(0deg); }
    94.35%  { transform:scale(1) rotateZ(0deg); }
  }

  .bounce {
    animation: bounce 0.25s ease;
  }